html {
  --color-hover: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  display: block;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--color-hover);
}

.is-active {
  color: var(--color-hover);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
